import Vue from 'vue'
import Router from 'vue-router'
import head from '../components/Header/head.vue'							//
import login from '../page/user/login.vue'
import mainuser from '../page/user/mainuser.vue'
import me from '../page/user/me.vue'
import addr from '../page/user/useraddr.vue'
import  full_module from '../page/commodity_datails/full_module.vue'
import cart_end from '../page/shopping_cart/cart_end.vue'
import cart from '../page/shopping_cart/cart.vue'
import product from '../page/produce/product.vue'							//
import comment from '../page/produce/comment.vue'							//	
import main from '../page/main/main.vue'
import allOrder from '../page/order/allOrder.vue'
import KB from '../page/order/kongbai.vue'

Vue.use(Router)
/**
 *   routes
 *  
 */
export default new Router({
	mode: 'history',
  routes: [
		//{ }vue
		{
			path: '/login',									//
			component: login						//
		},
		{
			path: '/index',									//
			component: mainuser						//
		},
		{
			path: '/me',									//
			component: me						//
		},
		{
			path: '/addr',									//
			component: addr						//
		},
		{
			path:'/',									//
			component:main						//
		},{
			path:'/head',
			component:head
		},{
			path:'/order',
			component:allOrder
		},{
			path:'/kongbai',
			component:KB
		},
		
		{
			path:'/product',									//
			component:product					//
		},
		{
			path:'/comment',									//
			component:comment					//
		},
			{
			  path: '/detail',
			  component: full_module
			},
			{	
			  path: '/cart',
			  component: cart_end
			}
  ]
})



