<template>
	 <div class="layui-col-xs6 layui-col-sm6 layui-col-md6  right">
			 <h1  class="product_subtitle">{{singerCom.name}}</h1>
			 <h2 class="product_name">{{singerCom.engname}}</h2>
			 <div class="start">
				 <span v-for=" n  in  5" :key=n>
					 <i class="layui-icon layui-icon-rate-solid start_icon" ></i>
				 </span>
				<span class="comment">{{sum_comment}}</span>
			 </div>
			 <div class="excerpt_details">
				 <span>
                 
              - HEPES
                - <span>...</span></span>
				<a href="#" class="more_link" data-description-target=".product_tabs_holder"> &gt;</a>
			 </div>
			 <div class="capacitys" > <!--  -->
				 <div class=" capacity_ml"  :class="{'capacity_chage':curindex==index}"    @click="chose_capacity(index,$event)" v-for=" (item,index) in singerCom.capacity_arr" :key="item.capacity">{{item.capacity}}ml</div>
			 </div>
			 <div class="capacitys" >
				 <div class="btn">
					 <div class="number" >
					<input class="input_nunmber" oninput="value=value.replace(/[^\d]/g,'')"  type="text"  value="" v-model="number" />
					 </div>
					 <div class="add">	
						 <button class="add_btn layui-btn layui-btn-sm"  @click="add_number">
							<i class="layui-icon">&#xe654;</i>
						  </button>
					 </div>
				 </div>
				<div class="btn price" >{{number*singerprice}}</div>
			 </div>
			  <div class="capacitys" >
				    <button class=" btn layui-btn  layui-btn-radius add_car" @click="submit_order"></button>
					  <button class=" btn layui-btn layui-btn-normal layui-btn-radius add_car" @click="addCart"></button>
			  </div>
			  <div id="ver_order" :class="hid==0?'ver_hid':'ver_show'">
				  <div class="order_tip"><h3></h3></div>
				  <p class="order_title"><span>:</span><span>{{orders.orders_number}}</span></p>
				  <p class="order_title"><span>:</span><span>hackerQ</span></p>
				  <p class="order_title"><span>:</span><span>110</span></p>
				  <p class="order_title"><span>:</span><span></span></p>
				  <p class="order_title">fuck</p>
				  <p class="order_sum">{{number}}{{number*singerprice}}</p>
				  
					 <div class="order_btn">
						<button class=" layui-btn  layui-btn-radius " ></button>
						<button class="  layui-btn layui-btn-normal layui-btn-radius " @click="order_cancel"></button>
					  </div>
					  
			  </div>
		
	</div>
</template>

<script>
	import {
		queryCommodity
	} from '../../request/api.js'
	 import {mapActions, mapGetters} from 'vuex'
	 import Stores from  '../../localStorage.js'
	export default{
		name:'right',
		data(){
			return{
				number:1,
				curindex:-1,
				singerprice:0,
				orders:{
					orders_number:'20190504252',
					name:'hackrQ',
					phone_number:110,
					address:''
				},
				sum_comment:6,
				hid:0,
				curCapacity:0,
				curColor:'',
				u_id:1,
               singerCom:[],
			   token:'',
			   product_id:'',
			   "id":1,
			   "pro_id":3,
			   /* trolleys:[] */  /*  */
			}
		},
		/* computed: {
		     ...mapGetters(['token']) 						//token
				}, */
		methods:{
			...mapActions(
				['getPicture','getName']							//
			),
			 chose_capacity(index,event){
				this.sum_price=this.singerCom.capacity_arr[index].prices*this.number;
				 this.curindex=index;
				 this.singerprice=this.singerCom.capacity_arr[index].prices;
				 this.curCapacity= parseInt( event.currentTarget.innerHTML.replace("ml",""))  ; //            
				 if(this.number>=this.singerCom.capacity_arr[index].restnumber){ /*  */
				 	this.number=this.singerCom.capacity_arr[index].restnumber;
				 }
			 },
			 add_number(){  /*  */
				 this.number=parseInt(this.number) +1;
				 if(this.number>=this.singerCom.capacity_arr[this.curindex].restnumber){
					 layer.alert('', {icon: 5});
					 this.number=this.singerCom.capacity_arr[this.curindex].restnumber;
				 }
			 },
			 submit_order(){
				 Stores.remove();
				 this.hid=1;
				
			 },
			 order_cancel(){
				 this.hid=0; 
			 },
			 querySingerCom(){ /*  */
				 queryCommodity(1).then(res =>{
					  this.singerCom=res;
					  this.getPicture(this.singerCom.picture);
					  this.getName(this.singerCom.name);
					   this.singerprice=this.singerCom.capacity_arr[0].prices; //
					   
				 }).catch( err => { console.log(err)})
			 },
			 addCart(){  //
			     if(this.curindex==-1){
					 layui.use('layer', function(){
                        var layer = layui.layer;
                        layer.msg('');
                     }); 
					  return;
				 }
				 if(this.token.trim()==""||this.token==null){ //localstorage
				    let trolleys=  Stores.fetch();
					/* let trolleys=this.trolleys;  
					console.log(this.curCapacity) */
				    let  trolley=trolleys.find( value => 
						value.id==this.id  &&value.capacity==this.curCapacity ) //
					let sum=this.number*this.singerprice
					
					let ids=this.id;   //id         //
		            let pro_ids=this.pro_id  ///
					 if(pro_ids==1){  //
                       this.product_id=ids.toString()+this.curColor;  //id
                     }else if(pro_ids==2){  //
                       this.product_id=ids.toString();  //id
                      }else if(pro_ids==3){  //
                       this.product_id=ids.toString()+this.curCapacity.toString();//id
                    }
					
					if(trolley){  /*  */
					  	 trolley.number+=parseInt(this.number)
						 trolley.sum_price+=sum;
					}
					 else{  /*  */
						trolley ={
							 product_id:this.product_id,  
							 id:this.id,  //id       //
							 pro_id:this.pro_id, //id
							 u_id:this.u_id,  //id
							 capacity:this.curCapacity,  //
							 picture:this.singerCom.picture,
							 name:this.singerCom.name,
							 price:this.singerprice,
							 number: this.number,
							 sum_price:sum,
							 notice: "",
							 isSelected:1  /*  */
						}
						trolleys.push(trolley);
					 }
					Stores.save(trolleys); 
					 layui.use('layer', function(){
                        var layer = layui.layer;
                       layer.msg('');
                     });  
					  
				 }else{/* redis */
				   let cart ={
				      	 product_id:this.product_id,
				      	 id:this.id,  //id     //
				      	 pro_id:this.pro_id, //id
				      	 u_id:this.u_id,  //id
				      	 capacity:this.curCapacity,  //
				      	 picture:this.singerCom.picture,
				      	 name:this.singerCom.name,
				      	 price:this.singerprice,
				      	 number: this.number,
				      	 sum_price:sum,
				      	 notice: "",
				      	 isSelected:1  /*  */
				      }
					   addCart(cart).then(res =>{
					  	   if(res==1){
							    layui.use('layer', function(){
							      var layer = layui.layer;
							     layer.msg('');
							   }); 
						   }
					  					   
					  }).catch( err => { console.log(err)})
					  
				 }
			 }
			 
		},
		mounted(){
			this.querySingerCom();
		}
		
   }
</script>

<style>
	.right{ /*  */
			 height: 450px;
			 /* background: #5FB878; */
			 font-family: "";
	}
	.product_subtitle{ /*  */
		font-weight: 400;
        text-transform: uppercase;
        margin: 0;
        font-size: 24px;
	}
	.product_name{  /*  */
	font-family: montserrat ultra light,sans-serif,microsoft yahei;
    font-weight: 400;
    text-transform: uppercase;
    font-size: 18px;
    color: #666;
    margin: 10px 0 0;
	}
	.start{  /*  */
		width: 80%;
		height: 80px;
		/* background: #C6E746; */
		 display: flex;
		align-items: center;
	}
	.start .start_icon,.start .comment{  /*  */
		font-size: 16px;
		letter-spacing:6px;
	}
	.start .start_icon{  /*  */
		color: #000;
	}
	.start .comment{ /*  */
       color: #666;
	   letter-spacing:3px;
	   margin-left: 5%;
	}
	.excerpt_details{  /*  */
		width: 80%;
		height: 60px;
		/* background: yellow; */
	display: inline-block;
    color: #000;
    font-size: 16px;
	font-family: "";
	text-indent : 21px;
	}
	.capacitys{ /* div */
		width: 90%;
		height: 45px;
		/* background: red; */
		 display: flex;
		justify-content:flex-start;
		align-items: center;
		margin-bottom: 5%;
		font-size: 18px;
	}
	.capacitys .capacity_ml{  /* div */
		width: 25%;
		height: 45px;
		/* background: #00FF00; */
		float: left;
		margin-right:4%;
		text-align: center;
		line-height: 45px;
		padding-left: 10px;
		box-sizing: border-box;
		font-size: 18px;
		border: 1px solid #8f6666;
		cursor:pointer;
	
	}
	.capacitys .btn{   /*  */
		width: 25%;
		height: 45px;
		margin-right:4%;
		font-size: 18px;
	}
	.capacitys .price{  /* div */
		text-align: center;
		line-height: 45px;
		color: #8f6666;
		border: 1px solid #8f6666;
		
	}
	.number{     /*  */
		width: 60%;
		height: 45px;
		float: left;
		background: greenyellow;
		text-align: center;
		line-height: 45px;
		font-size: 18px;
		border: 1px solid #8f6666;
		padding: 0;
	}
	.input_nunmber{ /*  */
		 width: 100%;
		 height: 45px;
		 float: left;
		 border-color: transparent;
		 text-align: center;
		 color: #8f6666;
	}
	.add{   /*  */
		width: 38%;
		height: 45px;
		float: left;
		background: beige;
		border-color: transparent;
	}
	.add_btn{   /*  */
		width: 100%;
		height: 100%;
	}
	.add_car{     /*   */
		color: black;
	}
	.capacity_chage{  /* div */
		background: linear-gradient(to bottom, #323232 0%, #3F3F3F 40%, #1C1C1C 150%), linear-gradient(to top, rgba(255,255,255,0.40) 0%, rgba(0,0,0,0.25) 200%);
 background-blend-mode: multiply;	
 color: white;
	}
	.price{   /*  */
		cursor: help;
	}
	#ver_order{   /* div */
		font-size: 18px;
		width: 50%;
		height: 260px;
		background: #ffffff;
		font-family:  "Comic Sans MS", cursive, "";
		position: relative;
		left: -20%;
		top:-80%;
		border: 1px solid #d8d8d8;
	}
	 .ver_show{
		opacity: 1;
		z-index: 0;
	 }
	 .ver_hid{
		 opacity: 0;
		 z-index: -10;
	 }
	 .order_tip{
		 background: #f8f8f8;
		 padding-top: 1%;
		 padding-bottom: 1%;
	 }
 .order_title{ /*  */
	 margin-left: 24%;
	 margin-bottom: 0.8%;
	
 }
 .order_sum{   /*  */
	 margin-top: 2%;
	 text-align: center;
 }
 .order_btn{  /*  */
	 width: 100%;
	 height: 50px;
	 /* background: red; */
	 display: flex;
	 margin-top: 1%;
	display: flex;
	 justify-content: center;
	 align-items: center;
 }
  #ver_order .layui-btn{  /*  */
	   font-size: 16px;
  }
</style>



