		methods: {
			register: function() {
				this.lastname = ''
				this.sex = ''
				this.phone = ''
				this.province = ''
				this.area = ''
				this.city = ''
				this.other = ''
				this.addshow = true
				this.updshow = false
				this.classStyle.display = 'block';
				document.body.style = 'overflow: hidden';
			},
			registerclose: function() {
				this.classStyle.display = 'none';
				document.body.style = 'overflow: auto';
			},
			addr: function() {
				// login().then(res => { //vuex
				console.log(this.userAll.address)
				this.addrs = this.userAll.address;
				// console.log(res.address)
				// }).catch(err => console.log())
			},

			//
			queryaddr: function(index) {
				// login().then(res => { //vuex
				this.classStyle.display = 'block';
				document.body.style = 'overflow: hidden';
				this.lastname = this.userAll.address[index].a_lastname
				this.sex = this.userAll.address[index].a_sex
				this.phone = this.userAll.address[index].a_phone
				this.province = this.userAll.address[index].province
				this.area = this.userAll.address[index].area
				this.city = this.userAll.address[index].city
				this.other = this.userAll.address[index].other
				this.addshow = false
				this.updshow = true
				this.index = index
				this.updateCity(this.province)
				this.updateDistrict(this.city)
				console.log(this.index)

				// 				setTimeout(function() {
				// 					layui.form.render(); //
				// 				}, 10);
				// console.log(this.address.province)
			},
			//
			deladdr: function(index) {
				address_delapi({
					a_id: this.userAll.address[index].a_id
				}).then(res => {
					this.userAll.address.splice(index, 1)
				})
			},
			//
			addaddr: function() {
				
				if(this.checked){
					this.moren = 1
				}else{
					this.moren = 0
				}
				
				let data = {
					u_id: this.userAll.u_id,
					a_lastname: this.lastname,
					a_sex: this.sex,
					province: this.province,
					city: this.city,
					area: this.area,
					other: this.other,
					a_moren: this.moren,
					a_phone: this.phone,
				}
				console.log(this.checked)
				console.log(this.moren)
				address_addapi(data).then(res => {
					this.classStyle.display = 'none';
					document.body.style = 'overflow: auto';
					this.userAll.address.splice(this.userAll.address.length - 1, 0, data)
				})
			},
			//
			updaddr: function() {
				
				if(this.checked){
					this.moren = 1
				}else{
					this.moren = 0
				}
				
				let data = {
					a_id: this.userAll.address[this.index].a_id,
					a_lastname: this.lastname,
					a_sex: this.sex,
					province: this.province,
					city: this.city,
					area: this.area,
					other: this.other,
					a_moren: this.moren,
					a_phone: this.phone,
				}
				address_updapi(data).then(res => {
					this.userAll.address[this.index].a_lastname = this.lastname
					this.userAll.address[this.index].a_sex = this.sex
					this.userAll.address[this.index].province = this.province
					this.userAll.address[this.index].city = this.city
					this.userAll.address[this.index].area = this.area
					this.userAll.address[this.index].other = this.other
					this.userAll.address[this.index].a_moren = this.checked
					this.userAll.address[this.index].a_phone = this.phone
					this.classStyle.display = 'none';
					document.body.style = 'overflow: auto';
				})
			},
			//
			morenaddr: function(index,moren) {
				let a_id = 
				morenaddrapi({
					a_id: this.userAll.address[index].a_id,
					a_moren: moren
				}).then(res => {
					for(let i = 0; i < this.userAll.address.length; i++){
						if(this.userAll.address[i].a_moren == 1){
							console.log(i)
							this.userAll.address[i].a_moren = 0
						}
					}
					this.userAll.address[index].a_moren = 1
					this.$router.replace("/addr")
				})
			},
			updateCity: function(province) {
				for (var i in this.arr) {
					if (this.arr[i].name == province) {
						console.log(province)
						this.cityArr = this.arr[i].sub;
						break;
					}
				}
				this.city = this.cityArr[i].name;
			},
			updateDistrict: function(city) {
				for (let i in this.cityArr) {
					if (this.cityArr[i].name == city) {
						this.districtArr = this.cityArr[i].sub;
						break;
					}
				}
				if (this.districtArr && this.districtArr.length > 0 && this.districtArr[1].name) {
					this.area = this.districtArr[i].name;
				} else {
					this.area = '';
				}
			}

		},
		watch: {
			// 			prov: function () {
			// 				this.
			// 			}
			province: function(newQuestion, oldQuestion) {
