import Vue from 'vue'
import Vuex from 'vuex'
import * as getters from './getters.js'
import * as actions from './actions.js'
import * as mutations from './mutations.js'

Vue.use(Vuex) 

//vuex


//state
const state={
	//
	nav_msg:{								
		attendCount:0,
		fansCount:0,
		blogCount:0
	},
	userAll:{
		u_id: '',
		u_lastname: '',
		u_sex: '',
		u_phone: '',
		u_birth: '',
		u_idcard: '',
		u_status: '',
		u_email: '',
		u_pwd: '',
		username: '',
		address: '',
	},
	token: '',
	picture:""			,//,
	name:"",
	

}

//
const store=new Vuex.Store({
	state,    // state
    getters,  // 
    actions,  // 
    mutations  // state
})
export default store


