<template>
<div class="layui-fluid"> 
	<div class="layui-row">
       <div class="layui-col-md12 cart">
		  <div class="layui-col-md11 cart_main "> <!-- rules=rows cellspacing=0 -->
			   <table class="cart_table" border="1"   rules=rows cellspacing=0>
				  <thead>
						<tr>
							<th class="Allchose"><input type="checkbox" @click="choseAll" ref="selectAllTop" class="checkbtn" /></th>
							<th colspan="2"></th>
							<th></th>
							<th></th>
							<th></th>
							<th></th>
							<th></th>
						</tr> 
					</thead>
					<tbody >
						<tr v-for="(item,index) in cart_info"  :key="item.product_id" >
							<td><input type="checkbox" ref="choseSinger"  @click="selectSingle(index)" class="checkbtn" :value="item" v-model="selected"/></td>
							<td class="goodsImgTd"><img :src="item.picture" class="goodsImg" alt=""></td>
							<td class="goodsName">{{item.name}}</td>
							<td>{{item.price}}</td>
							<td>{{item.number}}</td>
							<td>{{item.sum_price}}</td>
							<td>{{item.notice}}</td>
							<td><button class="layui-btn  " @click="delCart(index)"><i class="layui-icon">&#xe640;</i> </button></td>
						</tr>
					</tbody>
					</table>
			</div>
	 </div>
  </div>
	
	<div class="layui-row">
		<div class="layui-col-md12 cart_opper">
			<span class="sum"> {{totalNumber}}   {{totalPrice}}</span>
			<button class="layui-btn layui-btn-normal layui-btn-radius oper_btn txt_size" @click="delPart"></button>
			  <button class="layui-btn layui-btn-danger layui-btn-radius txt_size" @click="delAll"></button>
		</div>
		<div class="layui-col-md12 cart_opper " >
			<span class="sum"></span>
			<button class="layui-btn layui-btn-danger layui-btn-radius txt_size"></button>
		</div>
		<div class="layui-col-md12 cart_opper space" >
			<button class="layui-btn layui-btn-normal   end_btn"></button>
		</div>
	</div>
 </div>
</template>

<script>
	import {
		addCart,queryCart
	} from '../../request/api.js'
	 import Stores from  '../../localStorage.js'
	export default{
		 name:'cart',
		 data(){
			 return{
				   cart_info:[],
					 selected:[],//
					 token:'',
					 u_id:'',
					 curindex:-1,
					 key:[],
					 noDelProduct:[] /*  */
					
			 }
		 },
		 computed:{
			 totalNumber(){
				 return this.selected.reduce((a,b) =>parseInt(a)+parseInt(b.number),0)
			 },
			  totalPrice(){
			 	 return this.selected.reduce((a,b) =>parseFloat(a)+parseFloat(b.sum_price),0)
			 }
		 },
		 methods:{
				loadCart(){ //
					if(this.token.trim()==""||this.token==null){  //
					      
						    this.cart_info=Stores.fetch();
							this.selected=this.cart_info;
							this.$refs.selectAllTop.checked=true;
							for(let i=0;i<this.cart_info.length;i++){
									this.cart_info[i].isSelected=1					       
							}
					}else{//
						queryCart(this.u_id).then(res =>{  /* localstorage */
					         this.cart_info=res;
					         this.selected=this.cart_info;
					         this.$refs.selectAllTop.checked=true;
					         for(let i=0;i<this.cart_info.length;i++){
					         		this.cart_info[i].isSelected=1					       
					         }
				       }).catch( err => { console.log(err)})
					}
				},
				delCart(index){   /*  */
					if(this.token.trim()==""||this.token==null){  //
						    this.cart_info.splice(index,1);  
								Stores.save( this.cart_info)
					}else{//
						delCart(this.cart_info[index].product_id,this.u_id).then(res =>{
								    this.cart_info.splice(index,1);  
						}).catch( err => { console.log(err)})
					}
				},                  
				choseAll(){   /*  */
					if(this.selected.length!==Stores.fetch().length){  /*  */
						this.$refs.choseSinger.checked=true;
						this.selected=this.cart_info;
						for(let i=0;i<this.cart_info.length;i++){
								this.cart_info[i].isSelected=1					       
						}
					}else{   /*  */
					       if(this.cart_info.length==1){
							   this.$refs.choseSinger.checked=true;
						   }
							this.selected=[];
							for(let i=0;i<this.cart_info.length;i++){
								this.cart_info[i].isSelected=0					       
							}
							
					}
				},
				selectSingle(index){  /*  */
	                 		if(this.cart_info[index].isSelected!=1){
								this.cart_info[index].isSelected=1;
							}else{
								this.cart_info[index].isSelected=0;
							}
								console.log(this.cart_info[index].isSelected)
							for(let i=0;i<this.cart_info.length;i++){
								 if(this.cart_info[i].isSelected==0){
									 this.$refs.selectAllTop.checked=false;
									return
								 }		
								   this.$refs.selectAllTop.checked=true;
							}
							
				},
				delAll(){ /*  */
					if(this.token.trim()==""||this.token==null){  //  
					          this.cart_info=[];
								Stores.remove();
					}else{//
					for(let i=0;i<this.cart_info.length;i++){
					      this.key.push(this.cart_info[i].product_id)
					}
					cleanCart(this.u_id,this.key).then(res =>{
							 this.cart_info=[];
					}).catch( err => { console.log(err)})
						
					}
				},
				delPart(){ //
				this.noDelProduct.splice(0,this.noDelProduct.length)
					if(this.token.trim()==""||this.token==null){  //  
                           for(let i=0;i<this.cart_info.length;i++){
							       if(this.cart_info[i].isSelected==1){
									    this.cart_info.splice(i,1); /*  */
										i--;
								   }
						   }
						  Stores.save( this.cart_info)
					}else{//
					for(let i=0;i<this.cart_info.length;i++){
							if(this.cart_info[i].isSelected==1){
		                         this.key.push(this.cart_info[i].product_id)
							}
					}
						partCart(this.u_id,this.key).then(res =>{
							for(let i=0;i<this.cart_info.length;i++){
								if(this.cart_info[i].isSelected==1){
								this.cart_info.splice(i,1); /*  */
									i--;
								}
							}
								
						}).catch( err => { console.log(err)})
						
					}
				}
				
		 },
		 mounted(){
			 	/* this.addToCart(); */
				this.loadCart();
				
		 }
	}
	
	$(function(){
			var h=$(window).height();
			$('body').height(h);
			
		
	})	
	 
	 
	 
</script>


<style>
	body{
		background: url(../../../static/images/cart/8.jpg);
		background-size: cover;
		

	}
	.layui-fluid{
		padding: 0;
	}
  .cart{
		/* background: red; */
		display: flex;
		justify-content: center;
		margin-bottom: 2%;
	}
	.cart_main{
		padding-top: 0;
		border: none;
		/* background: #00F7DE; */
		
	}
	.cart_opper{
		/* background:yellowgreen; */
		font-family:  "Comic Sans MS", cursive, "";
		font-size: 18px;
		color: white;
		 /* background: #f2f2f2; */
		
	}
	.cart_table{
		  width: 100%;
		  align-content: center;	
			font-family:  "Comic Sans MS", cursive, "";
      font-size: 18px;
			border-left-style: none;
			border-right-style: none;	
			 /* border-color: #e6e6e6; */
	}
	.cart_table tbody{  /*  */
		 text-align: center;
		 /* color: white; */
		 background-image: linear-gradient(120deg, #fdfbfb 0%, #ebedee 100%);
	}
	.checkbtn{
		 zoom: 180%;
		 color: red;
		
		  
		 
	}
	th{
		padding-top: 2%;
		 padding-bottom:2%; 
		 background: #f2f2f2;
	}
	td{
		padding-top: 1%;
		 padding-bottom:1%; 
	}
	.Allchose{
		 padding-top: 0;
		  padding-bottom:0; 
	}
	.goodsImgTd{
		width: 20%;
	}
	.goodsImg{
		width: 60%;
		height: 30%;
	}
	.goodsName{
		text-align: left;
	}
	.txt_size{
		font-size: 17px;
	}
	.sum{
		margin-left: 10%;
	}
	.oper_btn{
		margin-left: 40%;
	}
	.end_btn{
		margin-left: 80%;
		font-size: 18px;
	}
	.space{
		margin-bottom: 3%;
	}
</style>



